.container {
    width: 100%;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    padding: 10px;
}

.title,
.date {
    height: 18px;
    border-radius: 5px;
}

.title {
    width: 100%;
    margin-bottom: 8px;
}

.date {
    width: 50%;
}